.image {
  /* position: relative; */
  align-content: center;
  left: 0;
  max-width:100%;
  height:100%;
  width:100%;
  margin: 0 auto;
  overflow: hidden;
}
.figure{
  align-content: center;
  position: relative;
  max-width:100%;
}
@media (min-width: 80rem){
  .figureright{
    float:right;
    padding-left:1rem;
    margin-right: 0;
  }
}
@media (min-width: 80rem){
  .figureleft{
    float:left;
    margin-left: 0;
    max-width: min-content;
  }
}
@media (max-width: 80rem){
  .image{
    max-width: 100%;
    width:100%
  }
}
@media (min-width: 80rem){
  .sidebarcontainer{
    margin-left:-10%;
  }
}

.sidebar{
  background-color: rgb(146, 203, 153);
  padding-left:1rem;
  padding-right:1rem;
  float:right;
  margin-left: .5rem;
  max-width: 55%;
  margin-bottom: .5rem;
}
@media (min-width: 80rem){
  .sidebar{margin-right: 0;}
}
@media (max-width: 80rem){
  .sidebar{
    max-width: 100%;
    width:100%;
  }
}
/* .sidebar:type */
.sidebar>h2:first-child{
  margin-top:1.25rem;
}
.sidebar::before{
  margin-left:.75rem;
}
